.about {
    width : 100%;
    text-align: center;
    position: relative;
    top: 40vh;
}

.about__container {
  background-color: whitesmoke;
  padding: 20px;
  border-radius: 5px;
  width: 100%;
  max-width: 400px;
  max-height: 300px;
  font-weight: 300;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
              0 8px 16px -8px rgba(0, 0, 0, 0.3), 
              0 -6px 16px -6px rgba(0, 0, 0, 0.025);
  display: inline-block;
}
  
.about__container span:first-child {
  font-size: 20px;
}

.about__container span:last-child {
  display: block;
  margin-top: 10px;
}

.about__title {
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 8px;
}

.about__description {
  font-size: 15px;
  font-weight: 400;
}