.nav {
    z-index: 1;
    position: fixed;
    top: 50px;
    left: 20px;
    display: flex;
    flex-direction: column;
    background-color: rgba(81, 81, 87);
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
                0 8px 16px -8px rgba(0, 0, 0, 0.3), 
                0 -6px 16px -6px rgba(0, 0, 0, 0.025);
    border-radius: 5px;
  }
  
  /* @media screen and (max-width: 1090px) {
    .nav {
      left: initial;
      top: initial;
      bottom: 0px;
      width: 100%;
    }
  } */
  
  .nav a {
    text-decoration: none;
    color: #ffffff;
    text-transform: uppercase;
    font-size: 12px;
    text-align: center;
    font-weight: 600;
    padding: 12px;
  }
  
  .nav a:not(:last-child) {
    border-bottom: 2px solid rgb(99, 99, 107);
  }